@media screen and (max-width: 100vh) {
    #top h1 {
        font-size: 4rem !important;
    }

    #top h2 {
        font-size: 2.25rem !important;
    }
}

#top {
    font-family: NotoSansMono, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #78B6E7;
    margin-top: 4rem;
    width: 100%;
    max-width: 100vw;
    text-align: center;
    padding: 4rem 0;
    clip-path: polygon(0% 100%, 0% 0%, 100% 0%, 100% 80%);
}

#top h1 {
    font-family: Igoe, system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 5rem;
    font-weight: 350;
    margin: 0;
}

#top h2 {
    font-size: 2.5rem;
    font-weight: 200;
    margin: 1rem 0;
    margin-bottom: 4rem;
}